input[type="radio"] + span {
  position: relative;
  display: inline-block;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 1px solid #ddd;
  transform: translate(0,4px);
  margin: 0 5px 0 0;
}
input[type="radio"] + span::after {
  content: "";
  position: absolute;
  width: 10px;
  height: 10px;
  left:50%;
  top: 50%;
  background: #007dbe;
  border-radius: 50%;
  opacity: 0;
  visibility: hidden;
  transform: translate(-50%,-50%);
  transition: .2s ease;
}
input[type="radio"]:checked + span::after {
  visibility: visible;
  opacity: 1;
}
